.Tweetable{
    background-color: #1a936f;
    border-radius: 4px;
    transition: 0.3s;
    border-bottom: 3px inset #10634a;
    -webkit-box-shadow: 0px 7px 12px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 7px 12px -10px rgba(0,0,0,0.75);
    box-shadow: 0px 7px 12px -10px rgba(0,0,0,0.75);
}

.Tweetable-Button{
    border: none;
    background-color: #1a936f;
    color: #f3e9d2;
    border-radius: 4px;
    transition: 0.3s;
    -webkit-box-shadow: 0px 7px 12px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 7px 12px -10px rgba(0,0,0,0.75);
    box-shadow: 0px 7px 12px -10px rgba(0,0,0,0.75);
}

.Tweetable-Button:hover{
    transition: 0.3s;
    transform: scale(1.02);
    cursor: pointer;
}

.Tweetable-Button:focus{
    outline: 0;
}

.Tweetable-Button:active{
    transition: 0.3s;
    outline: 0;
    transform: scale(0.9);
}


.twitter{
    background-color: #1DA1F2;
}

.instagram{
    background: linear-gradient(120deg, #405DE6 10%, #5851DB 20%, #833AB4 50%, #C13584 66.5%, #E1306C 83%, #FD1D1D 100%);
}